.header {
	height: 80px;
	display: flex;
	padding: 0 22px;
	align-items: center;
	background-color: #fff;
	justify-content: space-between;
}
 
.title {
	font-size: 18px;
	font-weight: 400;
	line-height: 28px;
 
	&:hover {
	  color: #1890ff;
	}
}
 
.btnsWrapper {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
 
.userInfo {
	display: flex;
	align-items: center;
 
	img {
		width: 46px;
		height: 46px;
		margin-left: 13px;
		margin-right: 27px;
		border-radius: 50%;
	}
}
 
.username {
	font-size: 18px;
	line-height: 28px;
	color: rgba(0, 0, 0, 0.85);
}
 
.logOut {
	cursor: pointer;
	font-size: 18px;
	line-height: 28px;
	border-radius: 5px;
	background-color: #fff;
	padding: 6px 18px 10px;
	color: rgba(0, 0, 0, 0.75);
	border: 1px solid rgba(0, 0, 0, 0.75);
 
	&:hover {
		color: #fff;
		background-color: rgba(0, 0, 0, 0.75);
	}
}
 
.createArticle {
	font-size: 14px;
	line-height: 22px;
	margin-right: 27px;
	padding: 6px 10px 5px;
	color: rgba(0, 0, 0, 0.85);
	border: 1px solid transparent;
 
	&:hover {
		color: #52c41a;
	}
}
 
.active {
	color: #52c41a;
	font-size: 14px;
	line-height: 22px;
	border-radius: 5px;
	margin-right: 27px;
	padding: 6px 10px 5px;
	background-color: #fff;
	border: 1px solid #52c41a;
 
	&:hover {
		color: #52c41a;
	}
}